import React from 'react'

class App extends React.Component {
    constructor(){
        super()
        this.state = {
            data : {}
        }
    }

    componentDidMount() {
        fetch("https://swapi.dev/api/people/1")
            .then(response => response.json())
            .then(data => this.setState({
                data : data
            }))
    }

    render(){
        return (
            <div>
                {this.state.data && <h1>{this.state.data.name}</h1>}
            </div>
        )
    }
}

export default App